<script>
  import Header from "./header.svelte";
  import BarChart from "./barChart.svelte";
  import PieChart from "./pieChart.svelte";
  import axios from "axios";
  import { onMount, beforeUpdate } from "svelte";

  let show = false;
  let covidData = [];
  let totalData = [];
  // $: visit = count;
  $: covidData;
  $: totalData;

  const handleClick = () => {
    show = !show;
  };

  onMount(async () => {
    await axios
      .get(`https://api.covid19api.com/total/country/india`)
      .then(res => (covidData = res.data))
      .catch(error => console.error(error));
  });

  beforeUpdate(async () => {
    await axios
      .get(`https://api.covidindiatracker.com/total.json`)
      .then(res => (totalData = res.data))
      .catch(error => console.error(error));
  });
</script>

<style>
  main {
    text-align: center;
    padding: 1em;
    max-width: 240px;
    margin: 0 auto;
    background-color: #e0e0e0;
    height: auto;
    font-family: cursive;
  }

  .chart-container {
    margin-top: 30px;
    display: flex;
    padding: 10px;
  }

  .info-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .info-container .info-item {
    display: flex;
    flex-direction: column;
    flex: 1 1 300px;
    margin: 1em;
    box-shadow: 0 0px 10px 1px rgba(0, 0, 0, 0.2);
    align-items: center;
    border-radius: 8px;
    color: #0171ba;
    text-align: center;
    padding: 20px;
    transition: 1s ease;
  }

  .red {
    color: red !important;
  }

  .green {
    color: green !important;
  }

  .orange {
    color: orange !important;
  }

  .items {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    flex-grow: 1;
    border: 1px solid #ffb300;
    box-shadow: 0px 0px 0px 2px #ffb300;
  }

  .btn {
    border-radius: 8px;
    background-color: darkgrey;
    color: white;
    font-family: cursive;
    font-weight: 700;
    cursor: pointer;
  }

  .img {
    height: 400px;
    width: 100%;
  }

  @media (min-width: 640px) {
    main {
      max-width: none;
    }
  }
  @media (max-width: 920px) {
    .chart-container {
      flex-direction: column;
    }
  }
</style>

<main>
  <Header />
  <button disabled={show} class="btn" on:click={handleClick}>
    India Covid Info
  </button>
  {#if show}
    <div class="info-container">
      <div class="info-item">
        Total Cases
        <strong>{totalData.confirmed}</strong>
      </div>
      <div class="info-item orange">
        Active Cases
        <strong>{totalData.active}</strong>
      </div>
      <div class="info-item green">
        Recoveries
        <strong>{totalData.recovered}</strong>
      </div>
      <div class="info-item red">
        Death
        <strong>{totalData.deaths}</strong>
      </div>
    </div>
    <div class="chart-container">
      <div class="items">
        <BarChart {covidData} />
      </div>
      <div class="items">
        <PieChart {covidData} />
      </div>
    </div>
  {/if}
</main>
